import {
    getBannerListService,
    getGoodsCateService,
    getActivityGoodsInfoService,
    getHotShopsService,
    addGoodsService,
    getSkuTypeNumService
} from "../../services/goodsService";
import {getCurrentPoints} from "../../services/MineService";
import {skipLink} from "../../utils/skipLinkManage";
import {getAreaListService, getBannerListMergedService} from "../../services/homeService";
import {Toast, PullRefresh, Dialog} from "vant";
import SwiperBanner from '../../common/swiper/MySwiper';
import Vue from 'vue';
import {mapActions, mapState} from "vuex";
import SpeechRecognizerDialog from "../../common/currency/SpeechRecognizerDialog";
import {storage} from '../../utils/storage';

export const productPage = {
    name: "productPage",
    data: function () {
        return {
            immediate: false,
            isShowVoiceBtn: false, //语音搜索入口
            skuTypeNum: 0, // 购物车种类数量
            totalPoints: 0, //当前积分数
            bannerData: [], // banner数据
            swipeIndex: 0, //banner 索引值
            goodsListData: [],  //商品列表数据
            setVantHeight: {},  //设置为你推荐的高度
            loading: false, //停止加载
            finished: false,//没有更多
            isLastPage: false,//是否有更多数据
            offsetHeight: 1000,
            goodsCateList: [],// 商品一级分类
            activityGoodsList: [],//限时秒杀列表
            current: 0, //按钮列表样式更替
            currentChange: false, //按钮列表更替中
            bannerChangeBool: false, //banner列表替换中
            shopList: [], //店铺列表
            columnList: [], // 精选专栏数据
            columnListWidth: {}, // 精选专栏列表宽度
            isLoading: false,//下拉刷新

            productScrollTop: 0, //页面滑动高度
            isShowTopBtn: false, //是否显示置顶按钮

            areaList: [],
            sendVal: false,
            recognizeResult: "正在聆听",

            bannerPicUrl: require("@/assets/images/home/open/banner_default.png"),
            catePicUrl: require("@/assets/images/mall/mall_sort_more.png"),

            statusLabel: {
                orange: require("@/assets/images/mall/mall_sale_orange.png"),
                red: require("@/assets/images/mall/mall_sale_red.png"),
            },


            areaCode: "",

            merchantLeft: 0, // 店铺滑动
            oldScrollLeft: 0,
            isScroll: false,
            seckillGoodsItem: "",
            rows: 10,
            changeInfoRows: 0,

            isShowCircle: false,
            addBtnIcon: null,
            isFirstLoading: false,
            addGoodsLoading: false,
            swiperFlag: true,

            // labelItemTop: this.$devicePlatform == "android" ? '2px' : '0' //add for ZHSQYYPT-6928
            labelItemTop:0,
        }
    },
    components: {
        'dialog-bar': SpeechRecognizerDialog,
        SwiperBanner
    },
    created() {
        this.areaCode = this.$userInfo.AREA_CODE
    },
    filters: {
        setSkuTypeNum(data) {
            return data >= 100 ? "99+" : data
        },
        threshold(data) {
            return data >= 10000 ?  (data/10000>=10?'9万+':Math.floor(data / 10000) + '万+') : data
        },
        priceFilters(data){
            return data >= 10000 ? Math.floor(data/10000*100)/100+ '万' : data
        }
    },
    mounted() {
        this.getBannerList();
        this.getGoodsCate();
        // this.getHotShops();  //获取店铺列表
        // this.getActivityGoodsInfo(); //获取限时秒杀列表
        // this.getColumnList(); //精选专栏
        this.loadRecommendGoods(1); //加载推荐商品
        this.getAreaList(); // 获取小区列表

        if (this.$devicePlatform == 'android') {
            let el = document.querySelector(".searchInput");
            if (el) {
                el.style.lineHeight = '35px';
            }
        }
    },
    activated() {
        this.current = 0;
        this.getSkuTypeNum(); //获取购物车数量
        this.getPointNum(); // 获取积分
        if (this.productScrollTop != 0) {
            this.$nextTick(() => {
                this.$refs.scrollContent.scrollTop = this.productScrollTop;
            })
        }
        if (this.areaCode != this.$userInfo.AREA_CODE) {
            this.areaCode = this.$userInfo.AREA_CODE
            this.getBannerList(); //获取banner列表
            this.getGoodsCate(); //获取商品一级分类
            // this.getHotShops();  //获取店铺列表
            // this.getActivityGoodsInfo(); //获取限时秒杀列表
            // this.getColumnList(); //精选专栏
            this.loadRecommendGoods(1); //加载推荐商品
            this.getAreaList(); // 获取小区列表
        }
        if (this.goodsListData.length != 0) {
            this.changeInfoRows = this.goodsListData.length
            this.getNewGoodsListData();
        }
    },
    watch: {
        merchantLeft(newValue, oldValue) {
            setTimeout(() => {
                if (newValue == this.$refs.merchantList.scrollLeft) { //延时执行后当newValue等于window.scrollY，代表滚动结束
                    console.log('滚动结束');
                    this.isScroll = false;
                    this.oldScrollLeft = newValue; //每次滚动结束后都要给oldScrollTop赋值
                } else {
                    console.log('滚动中');
                }
            }, 200); //必须使用延时器，否则每次newValue和window.scrollY都相等，无法判断，20ms刚好大于watch的侦听周期，故延时20ms
            if (this.oldScrollLeft == oldValue) { //每次滚动开始时oldScrollTop与oldValue相等
                console.log('滚动开始');
                this.isScroll = true;
            }
        },
        bannerData: {
            handler(newName, oldName) {
                this.swiperFlag = false
                this.$nextTick(() => {
                    this.swiperFlag = true
                })
            },
            immediate: true
        },
    },
    computed: mapState({
        pageNumber: state => state.productPage.pageNumber,
    }),
    methods: {
        ...mapActions(["getRecommendGoods","getnewRecommendGoods"]),
        // 进入前状态
        beforeEnter(el) {
            el.style.left = this.addBtnIcon.getBoundingClientRect().left + 'px';
            el.style.top = this.addBtnIcon.getBoundingClientRect().top + 'px';
            el.style.transform = 'scale(1)'
        },
        // 进入中
        enter(el, done) {
            // el.offsetWidth 强制html渲染动画
            // el.offsetWidth 这句话如何不写就不会有动画效果直接渲染的
            el.offsetWidth;
            el.style.left = this.$refs.cartIcon.getBoundingClientRect().left + 'px'
            el.style.top = this.$refs.cartIcon.getBoundingClientRect().top + 'px'
            el.style.transform = 'scale(.5)'
            done()
        },
        afterEnter(el) {
            this.isShowCircle = !this.isShowCircle
        },
        //置顶按钮
        goTopBtn() {
            this.$refs.scrollContent.scrollTop = 0;
        },
        //下拉刷新
        onRefresh() {
            this.rows = 10;
            this.getBannerList(); //获取banner列表
            this.getGoodsCate(); //获取商品一级分类
            // this.getHotShops();  //获取店铺列表
            // this.getActivityGoodsInfo(); //获取限时秒杀列表
            // this.getColumnList(); //精选专栏
            this.loadRecommendGoods(1); //加载推荐商品
            this.getAreaList(); // 获取小区列表
            this.current = 0;
            this.getSkuTypeNum(); //获取购物车数量
            this.getPointNum(); // 获取积分

            this.$refs.merchantList.scrollLeft = 0;
            this.$refs.choicenessScroll.scrollLeft = 0;
            this.$refs.seckillScroll.scrollLeft = 0;
        },
        //店铺滑动
        merchantScroll() {
            this.merchantLeft = this.$refs.merchantList.scrollLeft;
        },
        //页面滑动
        paperScroll() {
            this.scrollTop = this.$refs.scrollContent.scrollTop;
            if (this.scrollTop >= this.$refs.scrollContent.clientHeight) {
                this.isShowTopBtn = true;
            } else {
                this.isShowTopBtn = false;
            }
            if (this.scrollTop < (-50)) {
                this.onRefresh();
            }
        },
        /**
         * 获取小区列表
         * @param
         * OWNER_CODE  Number  业主CODE
         * CITY_CODE   Number  城市CODE
         * CITY_NAME   Number  城市名字
         */
        getAreaList() {
            let reqData = {
                OWNER_CODE: this.$userInfo.OWNER_CODE,
                CITY_CODE: this.$userInfo.CITY_CODE,
                CITY_NAME: this.$userInfo.CITY_NAME
            };
            getAreaListService(reqData)
                .then(noteData => {
                    if (noteData.length > 0) {
                        this.areaList = noteData;
                    }
                })
                .catch(error => {
                });
        },
        //广告跳转
        skipLinkFun(columnItem, isBanner = -1) {
            if (this.bannerChangeBool) {
                return;
            }
            if (isBanner != -1) {
                this.swipeIndex = isBanner
                console.log(this.swipeIndex)
            }
            this.productScrollTop = this.$refs.scrollContent.scrollTop;
            skipLink(this, this.areaList.length, columnItem)
        },
        //切换标签
        onChange(index) {
            this.current = index;
            this.currentChange = true;
            setTimeout(() => {
                this.currentChange = false;
            }, 500)
        },
        bannerChange() {
            this.bannerChangeBool = true;
            setTimeout(() => {
                this.bannerChangeBool = false;
            }, 500)
        },
        /**
         * 获取积分
         * @params
         * ownerCode  Number  业主CODE
         */
        getPointNum() {
            let paramData = {
                ownerCode: this.$userInfo.OWNER_CODE
            };
            getCurrentPoints(paramData).then(res => {
                this.totalPoints = res.pointBalance;
            })
                .catch(error => {
                });
        },
        // 上拉加载
        onLoad() {
            this.loadRecommendGoods(this.pageNumber, false)
        },
        /**
         * 获取首页banner列表
         * @param
         * AREA_CODE  Number      小区CODE
         * ADVERT_AREA   0     业主app标识
         * OWNER_CODE  Number  用户CODE
         **/
        getBannerList(isRefresh = true) {
            let paramData = {
                AREA_CODE: this.$userInfo.AREA_CODE,
                ADVERT_AREA: "0,7,9",
                OWNER_CODE: this.$userInfo.OWNER_CODE,
            }
            getBannerListMergedService(paramData).then(res => {
                if (res.ADVERT_1.length == 0) {
                    res.ADVERT_1.push({
                        PIC_URL: this.bannerPicUrl,
                    })
                    storage.setStorage('bannerListData', res.ADVERT_1);
                    storage.setStorage('areaCode', this.$userInfo.AREA_CODE);
                } else {
                    res.ADVERT_1.map((item, index) => {
                        if (index % 2 == 0) {
                            item.PIC_URL = item.PIC_URL.replace(/xwjfile.juhaolian.cn/, 'xwjfilea.juhaolian.cn')
                        }
                        Vue.prototype.$isHasLocalImage(item.PIC_URL, function (localImgUrl) {
                            item.PIC_URL = localImgUrl;
                        })
                    })
                }

                // if(isRefresh){
                this.bannerData = res.ADVERT_1;
                // }
                this.columnList = res.ADVERT_2;
                this.shopList = res.ADVERT_3;
                this.columnListWidth = {
                    'width': res.ADVERT_2.length * 240 + 'px'
                }
                this.isLoading = false;
            }).catch(error => {
                this.isLoading = false;
            });
        },
        /**
         * 精选专栏
         * AREA_CODE     Number  小区CODE
         * ADVERT_AREA   Number  专栏标识 7
         * OWNER_CODE    Number  业主CODE
         */
        getColumnList() {
            let paramData = {
                AREA_CODE: this.$userInfo.AREA_CODE,
                ADVERT_AREA: 7,
                OWNER_CODE: this.$userInfo.OWNER_CODE,
            }
            getBannerListService(paramData).then(res => {
                this.columnList = res;
                this.columnListWidth = {
                    'width': res.length * 240 + 'px'
                }
            }).catch(error => {
            });
        },
        /**
         * 添加购物车
         * @param
         * ownerCode Number  业主CODE
         * spuCode   Number  商品CODE
         * skuNo     Number  商品分类CODE
         * shopCode  Number  店铺CODE
         * skuCount  Number  商品数量
         * mark      Number  业主App标识
         */
        addGoods(spuItem, goodsIndex) {
            if (spuItem.storeNumber == 0) {
                Toast('该商品正在努力补货中哦')
                return;
            }
            if (this.addGoodsLoading || this.isShowCircle) {
                return;
            }
            console.log(spuItem)
            this.addBtnIcon = this.$refs.addBtn[goodsIndex];
            this.isShowCircle = true;
            this.addGoodsLoading = true;
            let paramData = {
                ownerCode: this.$userInfo.OWNER_CODE,
                spuCode: spuItem.spuCode,
                skuNo: spuItem.skuNo,
                shopCode: spuItem.shopCode,
                activitySpuId: spuItem.activitySpuId,
                skuCount: 1,
                mark: 1,
            }
            addGoodsService(paramData).then(res => {
                if (res.resultCode == "0") {
                    spuItem.cartCount += 1;
                    this.getSkuTypeNum(true);
                    this.addGoodsLoading = false;
                }
            }).catch(error => {
                this.addGoodsLoading = false;
            })
        },
        /**
         * 查询购物车数量
         * ownerCode  Number  业主CODE
         */
        getSkuTypeNum(isAddGoods = false) {
            let paramData = {
                ownerCode: this.$userInfo.OWNER_CODE
            }
            getSkuTypeNumService(paramData).then(res => {
                if (res.resultCode == 0) {
                    if (isAddGoods) {
                        Toast({
                            message: '添加购物车成功',
                            duration: 1000
                        });
                    }
                    this.skuTypeNum = res.data.skuTypeNum;
                }
            }).catch(error => {
            })
        },
        /**
         * 获取推荐商品列表
         * @param
         * areaCode  Number      小区CODE
         * page   Number     页数
         * rows  Number  个数
         **/
        loadRecommendGoods(pageNumber, isRefresh = true) {
            let paramData = {
                areaCode: this.$userInfo.AREA_CODE,
                ownerCode: this.$userInfo.OWNER_CODE,
                page: pageNumber,
                rows: this.rows
            }
            this.getRecommendGoods(paramData).then(res => {
                if (res.resultCode == 0) {
                    if (res.data.list.length != 0) {
                        res.data.list.map((item, index) => {
                            if (index % 2 == 0) {
                                item.spuPicUrl = item.spuPicUrl.replace(/xwjfile.juhaolian.cn/, 'xwjfilea.juhaolian.cn')
                            }
                            Vue.prototype.$isHasLocalImage(item.spuPicUrl, function (localImgUrl) {
                                item.spuPicUrl = localImgUrl;
                            })
                        })
                    }
                    if (isRefresh) {
                        this.goodsListData = res.data.list;
                        this.$store.commit("pageNumChange", {
                            custPageNum: true,
                            pageNumber: 2
                        });
                        this.isFirstLoading = true;
                    } else {
                        this.goodsListData = this.goodsListData.concat(res.data.list)
                    }
                    this.loading = false;

                    this.finished = res.data.isLastPage;
                }
            }).catch(error => {
            });
        },
        getNewGoodsListData() {
            let paramData = {
                areaCode: this.$userInfo.AREA_CODE,
                ownerCode: this.$userInfo.OWNER_CODE,
                page: 1,
                rows: this.changeInfoRows
            }
            this.getnewRecommendGoods(paramData).then(res => {
                if (res.resultCode == 0) {
                    console.log(this.goodsListData);
                    console.log(res.data.list);
                    this.goodsListData.map((item, index) => {
                        res.data.list.map((resItem) => {
                            if (item.spuCode == resItem.spuCode) {
                                item.skuPrice = resItem.skuPrice
                                item.cartCount = resItem.cartCount;
                                item.storeNumber = resItem.storeNumber;
                            }
                        })
                    })
                    // this.$store.commit("pageNumChange", {
                    //     custPageNum: true,
                    //     pageNumber: 2
                    // });
                }
            }).catch(error => {
            });
        },
        /**
         * 查询一级分类
         * @param
         * 无参数
         */
        getGoodsCate(isRefresh = true) {
            let paramData = {};
            getGoodsCateService(paramData).then(res => {
                if (res.data.length > 0) {
                    res.data.map((item, index) => {
                        if (!item.catePic) {
                            item.catePic = this.catePicUrl;
                        } else {
                            if (index % 2 == 0) {
                                item.catePic = item.catePic.replace(/xwjfile.juhaolian.cn/, 'xwjfilea.juhaolian.cn');
                            }
                            Vue.prototype.$isHasLocalImage(item.catePic, function (localImgUrl) {
                                item.catePic = localImgUrl;
                            })
                        }
                    })
                }
                // if(isRefresh){
                this.goodsCateList = res.data;
                // }
            }).catch(error => {
            });
        },
        /**
         * 获取店铺列表
         * @params
         * areaCode
         */
        getHotShops() {
            let paramData = {
                areaCode: this.$userInfo.AREA_CODE
            };
            getHotShopsService(paramData).then(res => {
                console.log(res)
                if (res.resultCode == 0) {
                    this.shopList = res.data;
                }
            }).catch(error => {
            })
        },
        /**
         * 限时秒杀
         * @param
         * areaCode Number 小区CODE
         */
        getActivityGoodsInfo() {
            let paramData = {
                areaCode: this.$userInfo.AREA_CODE
            }
            getActivityGoodsInfoService(paramData).then(res => {
                if (res.resultCode == 0) {
                    this.activityGoodsList = res.data;
                }
            }).catch(error => {
            });
        },
        //去更多分类页
        goToMoreGoods(goodsCateList, goodsCateIndex) {
            if (this.currentChange) {
                return
            }
            this.productScrollTop = this.$refs.scrollContent.scrollTop;
            this.$router.push({
                name: 'moreClassify',
                params: {
                    goodsCateList: goodsCateList,
                    goodsCateIndex: goodsCateIndex
                }
            })
        },
        goToMoreGoodsItem() {
            if (this.currentChange) {
                return
            }
            this.productScrollTop = this.$refs.scrollContent.scrollTop;
            this.$router.push({
                name: 'moreClassify',
                params: {
                    goodsCateList: this.goodsCateList,
                    goodsCateIndex: 0
                }
            })
        },
        //去秒杀列表页
        goToFlashSaleList() {
            if (!this.$userInfo.ROOM_CODE) {
                Dialog.confirm({
                    message: "您未绑定房间，是否前往绑定？",
                    confirmButtonText: "前往"
                }).then(() => {
                    //跳转到绑定房产页面
                    this.$router.push("selectArea");
                }).catch(() => {
                });
                return
            }
            let paramData = {
                areaCode: this.$userInfo.AREA_CODE
                // areaCode:'20190121'
            }
            getActivityGoodsInfoService(paramData).then(res => {
                if (res.resultCode == 0) {
                    console.log(res)
                    let underway = 1;
                    this.activityGoodsList.map((activityGoodsItem) => {
                        if (activityGoodsItem.spuStatus == 1) {
                            underway = 0;
                            return;
                        }
                    })
                    if (underway == 1) {
                        console.log("未开始")
                    } else if (underway == 0) {
                        console.log("进行中")
                    }
                    this.productScrollTop = this.$refs.scrollContent.scrollTop;
                    this.$router.push({
                        name: 'flashSaleList',
                        params: {
                            firstTab: underway
                        }
                    })
                }
            }).catch(error => {
            });
        },
        //去限时秒杀商品页面
        goToFlashSaleGoods(activityGoodsItem) {
            if (!this.$userInfo.ROOM_CODE) {
                Dialog.confirm({
                    message: "您未绑定房间，是否前往绑定？",
                    confirmButtonText: "前往"
                }).then(() => {
                    //跳转到绑定房产页面
                    this.$router.push("selectArea");
                }).catch(() => {
                });
                return
            }
            this.productScrollTop = this.$refs.scrollContent.scrollTop;
            this.$router.push({
                name: 'flashSaleGoods',
                query: {
                    spuCode: activityGoodsItem.spuCode,
                    activitySpuId: activityGoodsItem.activitySpuId,
                    status: activityGoodsItem.spuStatus,
                    goodsUrl: activityGoodsItem.goodsPic
                }
            })
        },
        //去商品搜索页面
        goToGoodsSearch(speechTxt) {
            this.productScrollTop = this.$refs.scrollContent.scrollTop;
            this.$router.push({
                name: 'goodsSearch',
                params: {
                    speechTxt: speechTxt,
                    isRefresh: 1,
                }
            })
        },
        //语音搜索
        goToSpeech() {
            var _this = this;
            var ifly = cordova.plugins.IflyForXwjPlugin;
            _this.recognizeResult = "正在聆听";
            const platform = navigator.userAgent;
            const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
            if (isiOS) {
                _this.sendVal = true;
                ifly.startListen(function (result) {
                        if (result) {
                            if (_this.sendVal) {
                                var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g, "");
                                _this.recognizeResult = resultcatch;
                                _this.sendVal = false;
                                _this.goToGoodsSearch(resultcatch);
                            }
                        } else {
                            _this.sendVal = false;
                            console.log('没有值')
                            Toast('我没听清您说什么')
                        }
                    },
                    function (message) {
                        _this.sendVal = false;
                        console.log("err" + message);
                    }, false, true
                );
            } else {
                var list = [
                    'android.permission.RECORD_AUDIO'
                ];
                var permissions = cordova.plugins.permissions;
                permissions.requestPermissions(list, function (status) {
                    if (status.hasPermission) {
                        _this.sendVal = true;
                        ifly.startListen(function (result) {
                                if (result) {
                                    console.log(result);
                                    if (_this.sendVal) {
                                        var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g, "");
                                        _this.recognizeResult = resultcatch;
                                        _this.sendVal = false;
                                        _this.goToGoodsSearch(resultcatch);
                                    }
                                } else {
                                    _this.sendVal = false;
                                    console.log('没有值')
                                    Toast('我没听清您说什么')
                                }
                            },
                            function (message) {
                                _this.sendVal = false;
                                console.log("err" + message);
                            }, false, true
                        );
                    }
                }, function (status) {
                    alert("请打开麦克风权限");
                });
            }
        },
        //去商品详情页
        goToProduct(goodsItem) {
            if (goodsItem.activitySpuId) {
                this.$router.push({
                    name: 'flashSaleGoods',
                    query: {
                        spuCode: goodsItem.spuCode,
                        activitySpuId: goodsItem.activitySpuId,
                        goodsUrl: goodsItem.spuPicUrl
                    }
                })
            } else {
                this.$router.push({
                    name: 'goodsDetail',
                    params: {
                        spuCode: goodsItem.spuCode,
                        goodsUrl: goodsItem.spuPicUrl
                    }
                })
            }

        },
        goToShopPage(shopCode) {
            if (this.isScroll) {
                return;
            }
            // this.productScrollTop = this.$refs.scrollContent.scrollTop;
            this.$router.push({
                name: 'shopPage',
                params: {
                    shopCode: shopCode,
                }
            })
        },
        //去购物车页面
        goToShoppingCart() {
            // this.productScrollTop = this.$refs.scrollContent.scrollTop;
            this.$router.push({
                name: 'shoppingCart',
                params: {}
            })
        },
        //去积分签到页面
        goToPoint() {
            // this.productScrollTop = this.$refs.scrollContent.scrollTop;
            this.$router.push({
                name: 'myPoints',
                params: {}
            })
        },
        //去积分签到页面
        goToCouponCenter() {
            // this.productScrollTop = this.$refs.scrollContent.scrollTop;
            this.$router.push({
                name: 'couponCenter',
                params: {}
            })
        },
    },
    beforeDestroy: function () {
    },
    beforeRouteLeave(to, form, next) {
        this.productScrollTop = this.$refs.scrollContent.scrollTop;
        next();
    }
}
